import Vue from "vue";
import Router from "vue-router";

Vue.use(Router);
/**
 * Exporting routes of the application to be imported in main.js
 */
export default new Router({
    mode: 'history',
    routes: [
        {
            path: '/',
            component: () => import('../home/Auth/Auth.vue'),
            name: 'Authentication'
        },
        {
            path: '/forgot-password',
            component: () => import('../home/Forgot-Password/Forgot-Password.vue'),
            name: 'forgotPassword'
        },
        {
            path: '/reset-password',
            component: () => import('../home/Reset-Password/Reset-Password.vue'),
            name: 'resetPassword'
        },
        {
            path: '/user',
            component: () => import('../dashboard/user/userLayout.vue'), //Lazy Loading
            redirect: {name: 'userDashboard'},
            children: [
                {
                    path: 'dashboard',
                    component: () => import('../dashboard/user/Dashboard.vue'),
                    name: 'userDashboard'
                },
                {
                    path: 'edit-profile',
                    component: () => import('../dashboard/user/Edit-Profile.vue'),
                    name: 'editProfile'
                },
                {
                    path: 'change-password',
                    component: () => import('../dashboard/user/Change-Password.vue'),
                    name: 'changePassword'
                }, {
                    path: 'invite-friends',
                    component: () => import('../dashboard/user/Invite-Friends.vue'),
                    name: 'inviteFriends'
                }
            ]
        },
        {
            path: '/admin',
            component: () => import('../dashboard/admin/adminLayout.vue'),
            redirect: {
                name: 'adminDashboard'
            },
            children: [
                {
                    path: 'dashboard',
                    component: () => import('../dashboard/admin/Dashboard.vue'),
                    name: 'adminDashboard'
                },
                {
                    path: 'profile',
                    component: () => import('../dashboard/admin/profile/Profile.vue'),
                    name: 'adminProfile'
                }
            ]
        }
    ]
});
